.get-quote-simple {
  padding: 25px 0;
  position: relative;
  z-index: 2;
  background: url(~img/paw-background.png) top center no-repeat;

  @include for-tablet {
    padding: 55px 0 70px;
  }

  .container {
    position: relative;
    z-index: 1;
  }
}

.get-quote-simple__headline {
  font-family: $font__bold;
  font-size: 24px;
  line-height: 36px;
  color: $not__black;
  margin: 0 0 24px;
  display: block;
  text-align: center;

  @include for-tablet {
    font-size: 44px;
    line-height: 64px;
    margin: 0 0 40px;
  }
}

.get-quote-simple__link {
  @include base-button(arrow, pink);
}

