.for-block {
  @include for-tablet {
    background: url(~img/paws_bg.png) top center no-repeat;
  }
}

.for-block__heading {
  text-align: center;
  display: block;
  font-family: $font__bold;
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  padding: 32px 0;
  margin: 0;

  @include for-tablet {
    text-align-last: left;
    font-size: 36px;
    line-height: 50px;
    padding: 60px 0;
  }
}

.for_block__content {
  position: relative;
}

.for-block__item {
  display: flex;
  margin-bottom: 30px;
}

.for-block__img {

  margin-right: 17px;

  img {
    width: 134px;

    @include for-tablet {
      width: 222px;
    }

    @include for-desktop {
      width: 290px;
    }
  }
}

.for-block__item-heading {
  font-size: 18px;
  line-height: 30px;
  margin: 0 0 20px;
  font-family: $font__bold;
  color: $not__black;

  @include for-tablet {
    font-size: 24px;
    line-height: 36px;
    margin: 40px 0 20px;
  }
}

.for-block__item-text {
  font-size: 18px;
  line-height: 30px;
  color: $boring__gray;
}


.for-block__decor1 {
  @include for-tablet {
    width: 114px;
    height: 130px;
    position: absolute;
    top: 230px;
    left: 480px;
    background: url(~img/for-dogs-decor-1.svg) 0 0 no-repeat;
    background-size: 100%;
  }
}

