.cooper-story {

}

.cooper-story__head {
  margin-bottom: 60px;
}

.cooper-story__head-heading {
  font-family: $font__bold;
  font-size: 36px;
  line-height: 54px;
  color: $blue__normal;
  margin: 0 0 20px;
  display: block;

  @include for-tablet {

  }
}

.cooper-story__head-text {
  font-size: 18px;
  line-height: 30px;
  color: $not__black;
}

.cooper-card-michael {
  margin: 0 -15px;
  box-shadow: $card__shadow;
  padding: 50px 15px 0;
  background-color: #fff;
  margin-bottom: 90px;
  position: relative;


  @include for-tablet {
    margin: 0;
    border-radius: 20px;
    padding: 60px 140px 60px 80px;
    margin-bottom: 110px;
  }

  @include for-desktop {
    padding: 60px 180px 60px 110px;
  }

}

.cooper-card-pavel {
  margin: 0 -15px;
  box-shadow: $card__shadow;
  padding: 50px 15px 0;
  background-color: #fff;
  margin-bottom: 90px;
  position: relative;


  @include for-tablet {
    margin: 0;
    border-radius: 20px;
    padding: 60px 80px;
    margin-bottom: 110px;
  }

  @include for-desktop {
    padding: 60px 110px;
  }

}

.cooper-card__heading {
  font-size: 24px;
  line-height: 36px;
  color: $not__black;
  margin: 0 0 30px;
}

.cooper-card__text {
  font-size: 18px;
  line-height: 30px;
  color: $boring__gray;
}

.cooper-card-michael__photo {
  width: 262px;
  height: 262px;
  margin: 0 auto;

  @include for-tablet {
    position: absolute;
    width: 384px;
    height: 384px;
    right: -260px;
    bottom: 0;
    margin: 0;
  }

  img {
    width: 100%;
  }

}

.cooper-card-michael__pic {
  width: 200px;
  height: 134px;
  position: absolute;
  top: -35px;
  right: 15px;
  background: url(~img/cooper-steven.svg) 0 0 no-repeat;
  background-size: 100%;

  @include for-tablet {
    width: 330px;
    height: 227px;
    right: -170px;
  }
}

.cooper-card-michael__decor {
  @include for-tablet {
    position: absolute;
    width: 120px;
    height: 98px;
    bottom: -60px;
    right: -240px;
    background: url(~img/cooper-michael-decor.svg) 0 0 no-repeat;
    background-size: 100%;
  }
}

.cooper-card-pavel__photo {
  width: 316px;
  height: 436px;
  margin: 0 auto;
  transform: translateY(30px);

  @include for-tablet {
    position: absolute;
    width: 400px;
    height: 552px;
    bottom: -90px;
    left: -350px;
    bottom: 0;
    margin: 0;
  }

  img {
    width: 100%;
  }
}

.cooper-card-pavel__pic {
  position: absolute;
  width: 234px;
  height: 141px;
  top: -35px;
  right: 15px;
  background: url(~img/cooper-max-mobile.svg) 0 0 no-repeat;
  background-size: 100%;

  @include for-tablet {
    width: 290px;
    height: 380px;
    background: url(~img/cooper-max.svg) 0 0 no-repeat;
    left: -250px;
    right: auto;
    top: 0;
  }
}

.cooper-card-pavel__decor {
  @include for-tablet {
    position: absolute;
    width: 122px;
    height: 144px;
    bottom: 160px;
    right: -40px;
    background: url(~img/cooper-pavel-decor.svg) 0 0 no-repeat;
    background-size: 100%;
  }
}
