.beyond-block {
  background-color: $blue__baby-light;
  position: relative;
  overflow: hidden;
  z-index: 1;
  padding: 0 0 45px;

  .swiper-slide {
    height: auto;
  }

  .beyond__desktop {
    display: none;

    @include for-tablet {
      display: block;
    }
  }
  .beyond__mobile {
    display: block;

    @include for-tablet {
      display: none;
    }
  }



  // .swiper-pagination {
  //   position: absolute;
  //   text-align: center;
  //   transition: 300ms opacity;
  //   transform: translate3d(0, 0, 0);
  //   z-index: 10;
  //   background: red;
  //   height: 30px;
  //   background: red;
  //   z-index: 100000000;
  //   width: 100%;
  // }

  @include for-tablet {
    padding: 0 0 55px;
  }

  &:before {
    width: 120%;
    height: 380px;
    content: '';
    display: block;
    background: #fff;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 0% 0% 50% 50% / 0% 0% 50px 50px;
    z-index: -1;
  }
}

.beyond-heading {
  text-align: center;
  display: block;
  font-family: $font__bold;
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  padding: 32px 0;
  margin: 0;

  @include for-tablet {
    font-size: 36px;
    line-height: 50px;
    padding: 60px 0;
  }
}


.beyond-card {
  background-color: #fff;
  border-radius: 20px;
  padding: 40px 34px 34px;
  box-sizing: border-box;
  height: 100%;
  box-shadow: $card__shadow;

  @include for-tablet {
    padding: 40px 50px 60px;
  }

  img {
    margin-bottom: 40px;
  }

  h3 {
    font-family: $font__bold;
    font-size: 24px;
    line-height: 36px;
    color: $not__black;
    margin: 0 0 20px;
  }

  p {
    font-size: 18px;
    line-height: 30px;
    color: $boring__gray;
  }
}
