.get-quote-bottom {
  background-color: $blue__baby-light;
  padding-top: 25px;
  // error with container width

  @include for-tablet {
    padding-top: 55px;
  }

  .container {
    position: relative;
    z-index: 1;
  }
}

.get-quote-bottom-container {
  background: #fff;
  border-radius: 20px 20px 0px 0px;
  box-shadow: $card__shadow;
  padding: 50px 36px 220px;

  @include for-tablet {
    padding: 70px 110px 110px;
  }
}

.get-quote-bottom__headline {
  font-family: $font__bold;
  font-size: 24px;
  line-height: 36px;
  color: $not__black;
  margin: 0 0 20px;
  display: block;
  text-align: center;

  @include for-tablet {
    font-size: 44px;
    line-height: 64px;
    margin: 0 0 40px;
  }
}

.get-quote-bottom__link {
  @include base-button(arrow, blue);
}

@include for-small-desktop {
  .get-quote-bottom__decor-1,
  .get-quote-bottom__decor-2 {
    background: url(../img/get-quote-bottom__decor.svg) 0 0 no-repeat;
    background-size: contain;
    position: absolute;
    z-index: -1;
  }

  .get-quote-bottom__decor-1 {
    width: 471px;
    height: 337px;
    transform: rotate(37deg);
    left: -150px;
    top: -140px;
  }

  .get-quote-bottom__decor-2 {
    width: 654px;
    height: 468px;
    transform: rotate(-160deg);
    right: -150px;
    bottom: -230px;
  }
}

