.cat-dog-cta {
  padding: 25px 0;

  @include for-tablet {
    padding: 55px 0;
  }
}

.cat-dog__card {
  position: relative;
  background-color: #B1E9FF;
  overflow: hidden;
  height: 100%;
  font-size: 0;
  width: 540px;

  @include for-tablet {
    margin-bottom: 0;
    width: 100%;
    border-radius: 20px;
  }

  &.img-left {
    margin-left: -141px;

    @include for-tablet {
      margin-left: 0;
    }
  }

  &.img-right {
    margin-left: -15px;

    @include for-tablet {
      margin-left: 0;
    }
  }

  img {
    width: 200px;
  }
}

.cat-dog__card-img-box {
  display: inline-block;
  width: 45%;
  height: 100%;

  &.img-dog {
    background: url('~img/dog_cta_burst.jpg') center center no-repeat;
    background-size: contain;
  }

  &.img-cat {
    background: url('~img/cat_cta_burst.jpg') center center no-repeat;
    background-size: contain;
  }
}

.cat-dog__card-content {

  width: 55%;
  display: inline-block;
  box-sizing: border-box;
  // padding: 30px 60px 37px 0;

  .img-left & {
    padding: 30px 60px 37px 0px; // ?
    float: right;


    @include for-tablet {
      padding: 30px 46px 37px 0px;
    }

    @include for-desktop {
      padding: 30px 60px 37px 0px;
    }
  }

  .img-right & {
    padding: 30px 0px 37px 15px; // ?
    float: left;

    @include for-tablet {
      padding: 30px 0px 37px 46px;
    }

    @include for-desktop {
      padding: 30px 0px 37px 60px;
    }
  }
}

.cat-dog__heading {
  font-family: $font__bold;
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  margin: 0 0 16px;

  @include for-tablet {
    font-size: 36px;
    line-height: 50px;
    margin: 0 0 20px;
  }
}

.cat-dog__text {
  font-size: 18px;
  line-height: 30px;
  color: #708DB1;
  margin-bottom: 23px;
}

.cat-dog__link {
  @include base-button(normal, blue);
  width: 270px;

  @include for-tablet {
    width: 200px;
  }

  @include for-desktop {
    width: 270px;
  }
}

.cat-dog__decor1 {
  @include for-tablet {
    width: 65px;
    height: 33px;
    background: url(~img/cat-dog-decor1.svg) 0 0 no-repeat;
    background-size: 100%;
    position: absolute;
    bottom: 23px;
    right: 45px;
  }
}

.cat-dog__decor2 {
  @include for-tablet {
    width: 95px;
    height: 70px;
    background: url(~img/cat-dog-decor2.svg) 0 0 no-repeat;
    background-size: 100%;
    position: absolute;
    bottom: 10px;
    right: 27px;
  }
}
