.footer {
  margin: 0 auto;
  background-color: $blue__baby;
  position: relative;
  z-index: 1;

  @include for-tablet {
    background: $blue__baby url(../img/Paws.png) 76% 74% no-repeat;
    background-size: 700px;
  }
}

.footer-row {
  position: relative;
}

.footer-logo {
  margin: 32px 0 20px;

  @include for-tablet {
    margin: 40px 0 30px;
  }
}

.footer-description-text {
  color: $dark__gray;
  font-size: 18px;
  line-height: 30px;
  margin-bottom: 15px;
}

.footer-social {
  list-style: none;
  padding: 0;
  font-size: 0;
  margin-bottom: 35px;

  @include for-tablet {
    margin-bottom: 24px;
  }

  li {
    display: inline-block;

    a {
      width: 30px;
      height: 30px;
      margin-right: 20px;
      border-radius: 50%;
      display: block;
      transition: opacity 0.2s;

      &.footer-social__fb {
        background: url(~img/icon_facebook.svg) 0 0 no-repeat;
      }
      &.footer-social__ig {
        background: url(~img/icon_instagram.svg) 0 0 no-repeat;
      }
      &.footer-social__tw {
        background: url(~img/icon_twitter.svg) 0 0 no-repeat;
      }

      &:hover {
        opacity: 0.8;
      }
    }
  }
}



.footer-nav {
  @include for-tablet {
    padding-top: 60px;
  }
}

.footer-nav-column {
  margin-bottom: 45px;
}

.footer-nav-resources__heading {
  font-size: 18px;
  line-height: 30px;
  color: $blue__normal;
  font-family: $font__bold;
  margin: 0 0 10px;
}

.footer-nav-list {
  li {
    display: block;
    margin-bottom: 10px;

    a {
      font-size: 18px;
      line-height: 30px;
      text-decoration: none;
      color: $dark__gray;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.footer-bottom {
  height: 20px;
  background-color: #96DCFC;
  height: 50px;
  font-size: 0;

  p {
    font-size: 12px;
    line-height: 50px;
    color: $dark__gray;
    width: 50%;
    display: inline-block;

    @include for-tablet {
      font-size: 16px;
    }

    &:last-child {
      text-align: right;
    }
  }
}

.footer__animal {
  width: 316px;
  height: 316px;
  position: absolute;
  top: -180px;
  right: 50%;
  transform: translateX(50%);

  @include for-tablet {
     width: 400px;
     height: 300px;
     top: -228px;
     right: 0;
     transform: translateX(0);
  }

  &.cat1 {
    background: url(~img/footer_cat1.png) 0 0 no-repeat;
    background-size: 100%;
  }

  // others
}
