.resource-center {
  background-color: $blue__baby-light;
  overflow: hidden;

  .resource-center__desktop {
    display: none;

    @include for-small-tablet {
      display: block;
    }
  }

  .resource-center__mobile {
    display: block;

    @include for-small-tablet {
      display: none;
    }
  }

  // is blog post page
  &.is-blog-post-page {
    padding-bottom: 190px;
  }
}

.resource-center__heading {
  text-align: center;
  display: block;
  font-family: $font__bold;
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  padding: 32px 0;
  margin: 0;

  @include for-tablet {
    font-size: 36px;
    line-height: 50px;
    padding: 60px 0;
  }
}

.resource-center__card {
  border-radius: 20px;
  background-color: #fff;
  overflow: hidden;
  display: block;
  height: 100%;
  box-shadow: $card__shadow;
}

.resource-center__img {
  max-height: 220px;
  display: block;
  width: 100%; // !
}

.resource-center__content {
  padding: 25px 34px;

  @include for-tablet {
    padding: 30px 50px;
  }
}

.resource-center__content-heading {
  font-family: $font__bold;
  font-size: 20px;
  line-height: 30px;
  color: $not__black;
  margin: 0 0 10px;
}

.resource-center__content-link {
  font-size: 16px;
  line-height: 28px;
  color: $boring__gray;
  padding-right: 27px;
  background: url(~/img/link_arrow.svg) right center no-repeat;

  .resource-center__card:hover & {
    color: $blue__normal;
    background: url(~/img/link_arrow_blue.svg) right center no-repeat;
  }
}

.resource-center-cta {
  padding: 30px 0;
  text-align: center;

  @include for-tablet {
    padding: 60px 0;
  }
}

.resource-center-cta__link {
  @include base-button(normal, pink);
}
