@mixin base-button($type: normal, $color: #000) {

  $color_hover: #000;

  $color_main: #000;
  $color_hover: #000;
  $color_active: #000;

  @if $color == blue {
    $color_main: #41b1f1;
    $color_hover: #1ca6f5;
    $color_active: #189ae4;
  }

  @if $color == yellow {
    $color_main: #fdc731;
    $color_hover: #ffbe0a;
    $color_active: #f6b500;
  }

  @if $color == pink {
    $color_main: #ff6b7f;
    $color_hover: #ff566d;
    $color_active: #ff4861;
  }

  @if $color ==green {
    $color_main: #4ce2a7;
    $color_hover: #09da88;
    $color_active: #19ce87;
  }

  @if $type == normal {
    width: 300px;
    height: 70px;
    line-height: 70px;
    border-radius: 10px;

    @include for-tablet {
      width: 280px;
      border-radius: 20px;
    }
  }

  @if $type == mini {
    width: 180px;
    height: 50px;
    line-height: 50px;
    border-radius: 25px;

    @include for-tablet {
      width: 220px;
      height: 50px;
      line-height: 50px;
      border-radius: 25px;
    }
  }

  @if $type == arrow {
    width: 284px;
    height: 60px;
    line-height: 60px;
    border-radius: 10px;
    padding-right: 25px;

    @include for-tablet {
      width: 305px;
      height: 90px;
      line-height: 90px;
      border-radius: 20px;
    }

    @include for-desktop {
      width: 380px;
      height: 70px;
      line-height: 70px;
    }

    &:after {
      display: inline-block;
      content: '';
      width: 24px;
      height: 19px;
      background: url(~img/button_arrow.svg) 0 0 no-repeat;
      background-size: 100%;
      right: 20px;
      top: 22px;
      transition: all 0.2s;
      position: absolute;

      @include for-tablet {
        width: 30px;
        height: 23px;
        background: url(~img/button_arrow.svg) 0 0 no-repeat;
        right: 37px;
        top: 33px;
      }

      @include for-desktop {
        top: 22px;
      }
    }
  }

  @if $type == arrow-big {
    width: 284px;
    height: 60px;
    line-height: 60px;
    border-radius: 10px;
    padding-right: 25px;

    @include for-tablet {
      width: 305px;
      height: 90px;
      line-height: 90px;
      border-radius: 20px;
    }

    &:after {
      display: inline-block;
      content: '';
      width: 24px;
      height: 19px;
      background: url(~img/button_arrow.svg) 0 0 no-repeat;
      background-size: 100%;
      right: 20px;
      top: 21px;
      transition: all 0.2s;
      position: absolute;

      @include for-tablet {
        width: 30px;
        height: 23px;
        background: url(~img/button_arrow.svg) 0 0 no-repeat;
        right: 37px;
        top: 33px;
      }
    }
  }

  position: relative;
  background-color: $color_main;
  display: inline-block;
  text-align: center;
  color: #fff;
  font-size: 20px;
  text-decoration: none;
  box-shadow: 0px 16px 50px transparentize($color_main, 0.5);
  transition: all 0.2s;

  &:hover, &:focus {
    color: transparentize(#fff, 0.3);
    background-color: $color_hover;
    box-shadow: 0px 16px 50px transparentize($color_main, 0.3);

    &::after {
      opacity: 0.7;
    }
  }

  &:active {
    box-shadow: 0px 16px 50px transparentize($color_main, 0.5);
    background-color: $color_active;
    color: #fff;

    &::after {
      opacity: 1;
    }
  }
}


// 1024
@mixin for-small-tablet {
  @include media-breakpoint-up(md) {
    @content;
  }
}

// 1024
@mixin for-tablet {
  @include media-breakpoint-up(lg) {
    @content;
  }
}

// 1280
@mixin for-small-desktop {
  @include media-breakpoint-up(xl) {
    @content;
  }
}

// 1280
@mixin for-desktop {
  @include media-breakpoint-up(xxl) {
    @content;
  }
}

@mixin app-screen {

  box-shadow: inset 0px -4px 7px rgba(0, 0, 0, 0.08), 0px 18px 52px rgba(215, 228, 249, 0.75);
  background-color: #fff;
  padding: 17px;
  border-radius: 46px;
  display: inline-block;

  @include for-small-desktop {
    display: block;
    width: 264px;
  }

  @include for-desktop {
    width: 290px;
  }

  img {
    width: 100%;
    border-radius: 32px;
  }
}
