.community-slider {
  padding: 0 0 25px;

  @include for-tablet {
    padding: 0 0 55px;
  }
}

.community-slider__heading {
  text-align: center;
  display: block;
  font-family: $font__bold;
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  padding: 32px 0;
  margin: 0;

  @include for-tablet {
    font-size: 36px;
    line-height: 50px;
    padding: 60px 0 0;
  }
}

.community-slider__row {
  position: relative;
}

.community-slider__container {
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.community_big-paw {
  display: none;

  @include for-tablet {
    display: block;
    width: 400px;
    height: 420px;
    position: absolute;
    top: -110px;
    right: 110px;
    z-index: -1;
    background: url(~img/big-paw.svg) top right no-repeat;
  }
}

.community-slider__slide {
  display: flex;
  flex-direction: column;

  @include for-tablet {
    flex-direction: row;
  }
}

.community-slider__img-box {
  text-align: center;

  @include for-tablet {
    text-align: left;
  }

  img {
    width: 184px;

    @include for-tablet {
      width: 290px;
    }
  }
}

.community-slider__content {
  padding-top: 25px;

  @include for-tablet {
    margin-left: 60px;
    padding-top: 65px;
  }

  p {
    font-size: 18px;
    line-height: 30px;
    color: $boring__gray;
    margin-bottom: 30px;
  }
}

.community-slider__author {
  color: $boring__gray;
  font-size: 18px;
  line-height: 30px;
  font-family: $font__bold;
}

.community-slider__arr-left {
  width: 60px;
  height: 60px;
  background: url(~img/slider-arr-left.svg) 0 0 no-repeat;
  background-size: 100%;
  top: 80px;

  @include for-tablet {
    top: 50%;
  }

  &:after {
    display: none;
  }
}

.community-slider__arr-right {
  width: 60px;
  height: 60px;
  background: url(~img/slider-arr-right.svg) 0 0 no-repeat;
  background-size: 100%;
  top: 80px;

  @include for-tablet {
    top: 50%;
  }

  &:after {
    display: none;
  }
}

