.advantages-heading {
  text-align: center;
  display: block;
  font-family: $font__bold;
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  padding: 32px 0;
  margin: 0;

  @include for-tablet {
    text-align: left;
    font-size: 36px;
    line-height: 50px;
    padding: 60px 0;
  }

  @include for-small-desktop {
    padding: 0 0 60px;
  }
}

.advantages-card {
  padding: 0 0 40px;
  height: 100%;
  box-sizing: border-box;
  text-align: center;

  @include for-tablet {
    text-align: left;
    padding: 0 0 60px;
  }

  img {
    margin-bottom: 40px;
  }
}

.advantages-card__heading {
  font-family: $font__bold;
  font-size: 18px;
  line-height: 30px;
  color: $not__black;
  margin: 0 0 16px;

  @include for-tablet {
    font-size: 24px;
    line-height: 36px;
    margin: 0 0 20px;
  }
}

.advantages-card__text {
  text-align: left;
  font-size: 18px;
  line-height: 30px;
  color: $boring__gray;
}
