header {
  height: 80px;
  padding: 15px 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 3;
  transition: box-shadow 0.2s, background 0.2s;

  @include for-tablet {
    height: 130px;
    padding: 40px 0;
  }

  &.is-sticky {
    background-color: #fff;
    box-shadow: $card__shadow;
  }
}

.header-logo-container {
  display: inline-block;
  vertical-align: top;
}

.header-logo {
  width: 100px;
  margin-top: 8px;

  @media screen and (min-width: 414px) {
    margin-top: 5px;
    width: 130px;
  }

  @include for-tablet {
    height: 60px;
    margin-top: -5px;
    width: auto;
  }
}

.header-navigation {
  display: inline-block;
  vertical-align: top;
  text-align: right;
}

.main-nav {
  display: inline-block;
}

.main-nav-list {
  margin-right: 20px;
  font-size: 0;
  display: none;

  @include for-small-desktop {
    display: block;
  }

  li {
    display: inline-block;

    a {
      font-size: 18px;
      line-height: 20px;
      color: $not__black;
      padding: 25px 22px;

      &:hover {
        text-decoration: underline;
      }
    }

    &:last-child a {
      font-style: italic;
    }
  }
}

.header__cta {
  @include base-button(mini, yellow);
  width: 140px;
  font-size: 16px;

  @media screen and (min-width: 414px) {
    @include base-button(mini, yellow);
  }
}

.mobile-nav-button {
  width: 55px;
  height: 80px;
  background: transparent url('~img/mobile-menu-btn.svg') center center no-repeat;
  border: 0;
  padding: 0;
  cursor: pointer;
  outline: 0;
  margin-top: -15px;
  margin-left: -15px;


  @include for-small-desktop {
    display: none;
  }
}


.mobile_nav {
  position: fixed;
  top: 80px;
  left: 0;
  background-color: #fff;
  width: 100%;
  box-shadow: $card__shadow;
  display: none;

  @include for-tablet {
    top: 130px;
  }

  &.is-opened {
    display: block;
  }
}

.mobile_nav-list {
  li {
    height: 60px;
    border-top: 1px solid $boring__gray;
  }

  a {
    font-size: 18px;
    line-height: 60px;
    color: $not__black;
    padding-left: 50px;
    width: 100%;

    display: inline-block;
  }
}
