.blog-section-index {
  background-color: #fff;
  padding-top: 120px;
  position: relative;
  z-index: 1;
  padding-bottom: 190px;

  @include for-tablet {
    padding-top: 170px;
  }
}

.blog-section-post {
  background-color: #fff;
  padding-top: 120px;
  position: relative;
  z-index: 1;

  @include for-tablet {
    padding-top: 170px;
  }

  .blog-index__sidebar {
    display: none;

    @include for-small-tablet {
      display: block;
    }
  }

  .resource-center {
    padding-bottom: 190px;
  }
}


.blog-index__heading {
  font-size: 18px;
  line-height: 21px;
  color: $not__black;
  margin: 0 0 30px;
  font-family: $font__main;
  font-weight: 400;
  display: none; // !!!
}

.blog-index__item {
  margin-bottom: 40px;
}

.blog-index__item-heading {
  font-size: 18px;
  line-height: 30px;
  color: $not__black;
  margin: 0 0 10px;

  @include for-tablet {
    font-size: 24px;
    line-height: 36px;
    margin: 0 0 20px;
  }
}

.blog-index__item-box {

  @include for-tablet {
    display: flex;
  }
}

.blog-index__item-img {
  margin-bottom: 10px;


  img {
    width: 100%;
    border-radius: 10px;

    @include for-tablet {
      width: 300px;
      object-fit: cover;
      height: 220px;
    }

    @include for-desktop {
      width: 400px;
      object-fit: cover;
      height: 220px;
    }
  }

  @include for-tablet {
    margin-right: 24px;
  }
}



.blog-index__item-content {

}

.blog-index__item-text {
  font-size: 18px;
  line-height: 30px;
  color: $boring__gray;
  margin-bottom: 10px;
}

.blog-index__item-link {
  font-size: 16px;
  line-height: 28px;
  font-size: 16px;
  line-height: 28px;

  padding-right: 27px;
  color: $blue__normal;
  background: url(~/img/link_arrow_blue.svg) right center no-repeat;

  &:hover {
    text-decoration: underline;
  }
}



// blog post

.blog-post__heading {
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  margin: 0 0 30px;

  @include for-tablet {
    font-size: 36px;
    line-height: 54px;
    margin: 0 0 40px;
  }
}

.blog-post-content {
  h1, h2 {
    color: $not__black;
    font-size: 24px;
    line-height: 36px;
    margin: 40px 0 20px;
  }

  h3 {
    color: $not__black;
    font-size: 21px;
    line-height: 30px;
    margin: 40px 0 20px;
  }

  h4 {
    color: $not__black;
    font-size: 18px;
    line-height: 30px;
    margin: 40px 0 20px;
  }

  p {
    font-size: 18px;
    line-height: 30px;
    color: $boring__gray;
    margin: 0 0 20px;
  }

  a {
    color: $blue__normal;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }

  ul {
    margin: 0 0 20px;

    li {
      font-size: 18px;
      line-height: 32px;
      color: $boring__gray;
      padding-left: 25px;
      display: list-item;
      background: url(~img/read-more-paw.svg) 0 10px no-repeat;
      background-size: 12px;
    }
  }

  ol {
    list-style: decimal;

    li {
      font-size: 18px;
      line-height: 32px;
      color: $boring__gray;
      padding-left: 15px;
      margin-left: 20px;
      display: list-item;
      background-size: 12px;
    }
  }

  strong, b {
    font-weight: bold;
  }

  i, em {
    font-style: italic;
  }
}

.blog-post__tags {
  margin: 40px 0;

  @include for-tablet {
    margin: 60px 0 10px;
  }

  ul {
    display: inline-block;

    li {
      display: inline;
    }
  }

  a {
    color: $not__black;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}

.blog-post__author {
  display: flex;

  img {
    width: 70px;
    margin-right: 20px;
  }
}

.blog-post__author-content {
  p {
    font-size: 18px;
    line-height: 24px;
    margin: 0;
    color: $not__black;
    padding-top: 11px;
  }

  span {
    font-size: 18px;
    line-height: 24px;
    margin: 0;
    color: $boring__gray;
  }
}









// read more
.read-more {
  display: none;

  @include for-small-tablet {
    display: block;
    margin-bottom: 60px;
  }
}

.read-more__heading {
  font-size: 24px;
  line-height: 36px;
  color: $blue__normal;
  font-family: $font__bold;
  margin: 0 0 30px;
}

.read-more__list {
 li {
   margin-bottom: 20px;
 }
}
.read-more__link {
  font-size: 18px;
  line-height: 32px;
  text-decoration: underline;
  color: $not__black;
  padding-left: 40px;
  display: inline-block;
  background: url(~img/read-more-paw.svg) 0 6px no-repeat;
  background-size: 20px;

  .read-more-dogs & {
    background: url(~img/read-more-bone.svg) 0 7px no-repeat;
  }

  .read-more-cats & {
    background: url(~img/read-more-paw.svg) 0 6px no-repeat;
  }

  &:hover {
    text-decoration: none;
  }
}


// get quote small
.get-quote-small {
  height: 250px;
  position: relative;
  box-shadow: $card__shadow;
  background: url(~img/get-quote-small.jpg) top center no-repeat;
  background-size: cover;
  margin: 0 -15px;

  @include for-tablet {
    margin: 0 0 70px;
    border-radius: 10px;
  }
}

.get-quote-small__heading {
  font-size: 18px;
  line-height: 24px;
  color: $not__black;
  width: 280px;
  max-width: 100%;
  padding-top: 18px;
  padding-left: 21px;
  font-weight: 400;
  margin: 0;

  strong {
    font-family: $font__bold;
  }
}

.get-quote-small__cta-box {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: -30px;
}

.get-quote-small__cta {
  @include base-button(arrow, green);
  height: 60px;
  line-height: 60px;
  width: 244px;
  border-radius: 10px;

  &:after {
    width: 24px;
    height: 19px;
    top: 22px;
    background-size: contain;
  }

  @include for-tablet {
    height: 60px;
    line-height: 60px;
    width: 244px;
    border-radius: 10px;

    &:after {
      width: 24px;
      height: 19px;
      top: 22px;
      background-size: contain;
    }
  }

  @include for-desktop {
    height: 60px;
    line-height: 60px;
    width: 244px;
    border-radius: 10px;

    &:after {
      width: 24px;
      height: 19px;
      top: 22px;
      background-size: contain;
    }
  }
}


// pagination

.blog-index-pagination {
  margin: 50px 0;
  text-align: center;

  @include for-tablet {
    text-align: left;
  }
}

.nav-pagination-list {
  font-size: 0;
  li {
    display: inline-block;
    margin-right: 18px;
    vertical-align: top;
  }

  a {
    display: inline-block;
    width: 32px;
    height: 30px;
    border-radius: 4px;
    border: 1px solid rgba(148, 162, 179, 0.3);
    text-align: center;
    font-size: 14px;
    line-height: 30px;
    font-weight: bold;
    color: $not__black;
    transition: all 0.2s;

    &:hover,
    &.nav-pagination__active {
      color: $blue__normal;
      border-color: $blue__normal;
    }

    &.nav-pagination__prev {
      border: 0;
      background: url(~img/nav-arrow-left.svg) center center no-repeat;
      margin-right: -10px;
    }

    &.nav-pagination__next {
      border: 0;
      background: url(~img/nav-arrow-right.svg) center center no-repeat;
      margin-left: -10px;
    }
  }
}



