.hero {
  background-color: #fff;
  padding-top: 120px;

  position: relative;
  z-index: 1;

  @include for-tablet {
    padding-top: 170px;
  }

  @include for-desktop {
    // margin-bottom: -60px; // howitworks conflict
  }

  &.hero-for-dogs,
  &.hero-for-cats {
    background: $gray__bg;

    &:after {
      width: 100%;
      height: 100px;
      content: '';
      background: #fff;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 50% 50% 0% 0% / 50px 50px 0% 0%;
      z-index: -1;

      @include for-tablet {
        height: 70px;
        width: 120%;
      }
    }
  }

}

.hero__content-heading {
  font-size: 28px;
  line-height: 34px;
  color: $not__black;
  padding: 0 0 16px;
  margin: 0;

  @include for-tablet {
    font-size: 44px;
    line-height: 54px;
    padding: 40px 0;
  }

  strong {
    font-family: $font__bold;
  }
}

.hero__content-text {
  font-size: 16px;
  line-height: 28px;
  color: $not__black;
  margin-bottom: 30px;

  @include for-tablet {
    margin-bottom: 60px;
    font-size: 18px;
    line-height: 30px;
  }
}

.hero__content-link {
  @include base-button(arrow-big, pink);
  margin-bottom: 34px;

  .hero-for-cats & {
    @include base-button(arrow-big, blue);
  }

  @include for-tablet {
    margin-bottom: 0;
  }
}

.hero__img {
  img {
    max-width: 100%;
  }
}
