.app-block {
  background-color: $blue__baby-light;
  padding: 0 0 25px;

  @include for-tablet {
    padding: 0 0 55px;
  }
}

.app-block__heading {
  text-align: center;
  display: block;
  font-family: $font__bold;
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  padding: 32px 0;
  margin: 0;

  @include for-tablet {
    font-size: 36px;
    line-height: 50px;
    padding: 60px 0;
  }
}

.app-block__container {
  position: relative;
  padding-top: 30px;
  margin-bottom: 50px; // ?

  @include for-tablet {
    margin-bottom: 235px;
  }
}


.app-block__covers {

}



.app-block__covers-card {
  display: flex;
  margin-bottom: 30px;

  @include for-tablet {
    margin-bottom: 32px;
  }

  @include for-small-desktop {
    margin-bottom: 90px;

    .col-lg-5:nth-child(1) &,
    .col-lg-5:nth-child(3) & {
      flex-direction: row-reverse;
      text-align: right;
    }
  }
  p {
    font-size: 18px;
    line-height: 30px;
    color: $boring__gray;
  }
}

.app-block__covers-card-box {

  margin-right: 17px;

  @include for-small-desktop {
    width: 220px;
  }

  img {
    width: 134px;

    @include for-tablet {
      width: 100%;
    }
  }


}

.app-block__covers-heading {
  font-family: $font__bold;
  font-size: 18px;
  line-height: 30px;
  color: $not__black;
  padding: 0 0 16px;
  margin: 0;

  @include for-tablet {
    font-size: 24px;
    line-height: 36px;
    padding: 27px 0 20px;
  }

  &:after {
    display: block;
    overflow: hidden;
    content: "";
    clear: both;
  }
}



.app-block__screen-block {
  @include app-screen;
  width: 340px;
  padding: 20px;
  margin: 0 auto 50px;
  position: relative;

  @include for-small-desktop {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.app-block__screen-decor {

  @include for-tablet {
    position: absolute;
    right: -32px;
    bottom: -22px;
    background: url(~img/app-block-decor.svg) 0 0 no-repeat;
    background-size: 100%;
    width: 184px;
    height: 91px;
  }
}

.app-block__cta-box {
  text-align: center;
}

.app-block__link {
  @include base-button(normal, green);
}
.app-block__link-description {
  display: block;
  margin-top: 20px;
  font-size: 18px;
  line-height: 30px;
  color: $boring__gray;
}
