body {
  font-family: $font__main;
  overflow-x: hidden;
}

p {
  margin: 0;
}

.container {
  --bs-gutter-x: 15px;
  padding: 0 15px;


  @include for-tablet {
    --bs-gutter-x: 24px;
    width: 100%;
    max-width: 960px;
    padding: 0 32px;
  }

  @media screen and (min-width:1440px)  {
    --bs-gutter-x: 40px;
    max-width: 1280px;
  }


  // $container-max-widths: (
  //   sm: 540px,
  //   md: 720px,
  //   lg: 960px,
  //   xl: 1140px,
  //   xxl: 1320px);
}

* {
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}


.align-center {
  text-align: center;
}

.align-right {
  text-align: right;
}


