.get-quote-full {
  background-color: $blue__baby-light;
  position: relative;
  z-index: 1;
  padding: 25px 0 60px;

  @include for-tablet {
    padding: 55px 0 90px;
    overflow: hidden;
  }

  &:after {
    width: 100%;
    height: 100px;
    content: '';
    background: #fff;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 50% 50% 0% 0% / 50px 50px 0% 0%;
    z-index: -1;

    @include for-tablet {
      height: 380px;
      width: 120%;
    }
  }

  &.inverse-bg {
    background: $blue__baby-light;

    &:before {
      width: 100%;
      height: 250px;
      content: '';
      display: block;
      background: #fff;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 0% 0% 50% 50% / 0% 0% 50px 50px;
      z-index: -1;
    }

    &:after {
      display: none;
    }
  }
}

.get-quote-full__box {
  height: 240px;
  position: relative;
  box-shadow: $card__shadow;
  background: url(~img/get_quote_full.jpg) top center no-repeat;
  background-size: cover;
  margin: 0 -15px;

  @include for-tablet {
    height: 500px;
    margin: 0;
    border-radius: 20px;
  }
}

.get-quote-full__heading {
  font-size: 18px;
  line-height: 24px;
  color: $not__black;
  width: 180px;
  padding-top: 18px;
  padding-left: 21px;
  margin: 0;

  @include for-tablet {
    font-size: 44px;
    line-height: 54px;
    padding-top: 50px;
    padding-left: 70px;
    width: 400px;
  }

  strong {
    font-family: $font__bold;
  }
}

.get-quote-full__cta-box {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: -30px;

  @include for-tablet {
    position: absolute;
    bottom: 40px;
  }
}
.get-quote-full__cta {
  @include base-button(arrow, yellow);
}

.get-quote-full__decor {
  display: none;

  @include for-tablet {
    width: 270px;
    height: 150px;
    position: absolute;
    right: 10px;
    bottom: -50px;
    background: url(~img/get_quote_full_decor.svg) 0 0 no-repeat;
    display: block;
  }
}
