.howitworks {
  position: relative;
  z-index: 1;
  background-color: #fff;
  padding: 25px 0;
  overflow: hidden;

  @include for-tablet {
    padding: 55px 0;
  }

  &:before {
    width: 120%;
    height: 50px;
    content: "";
    display: block;
    background: #fff;
    position: absolute;
    bottom: 200px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 0% 0% 50% 50% / 0% 0% 50px 50px;
    z-index: -1;
  }

  &:after {
    width: 120%;
    height: 250px;
    content: "";
    display: block;
    background: $blue__baby-light;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: -2;
  }
}

.howitworks__row {
  margin-bottom: 40px;

  @include for-desktop {
    margin-bottom: -70px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.hero__img {
  position: relative;

  .decor-dots-blue {
    @include for-desktop {
      width: 291px;
      height: 182px;
      background: url(~img/decor-dots-blue.svg) 0 0 no-repeat;
      background-size: 100%;
      position: absolute;
      top: 210px;
      left: -100px;
    }
  }
}

.howitworks__content {
  position: relative;
  z-index: 1;
}

.howitworks__content__paw:after {
  width: 400px;
  height: 420px;
  background: url(~img/big-paw.svg) 0 0 no-repeat;
  background-size: contain;
  content: '';
  display: block;
  position: absolute;
  top: 60px;
  right: -100px;
  z-index: -1;

  display: none;
}

.howitworks__img-box {
  text-align: center;
  position: relative;
  z-index: 1;

  .howitworks__row-img-right & {
    transform: translateX(30px);

    @include for-small-tablet {
      transform: none;
    }
  }

  .howitworks__row-img-left & {
    transform: translateX(-70px);

    @include for-small-tablet {
    transform: none;
    }
  }
}

.howitworks__img {
  @include app-screen;
  margin: 0 auto;

  img {
    width: 192px;

    @include for-small-desktop {
      width: 230px;
    }

    @include for-desktop {
      max-width: 100%;
      width: 100%;
    }
  }
}

.howitworks__decor-1 {
  position: absolute;
  transform: scale(-1, 1);
  background: url(~img/how-it-works__tail.jpg) 0 0 no-repeat;
  background-size: 100%;
  width: 394px;
  height: 194px;
  bottom: -64px;
  right: -142px;

  @include for-small-tablet {
    width: 394px;
    height: 194px;
    bottom: -64px;
    right: -32px;
    transform: none;
  }

  @include for-tablet {
    right: -22px;
  }

  @include for-small-desktop {
    width: 507px;
    height: 250px;
    bottom: -93px;
    right: -66px;
  }

  @include for-desktop {
    right: -12px;
  }

}

.howitworks__decor-2 {

  @include for-desktop {
    width: 43px;
    height: 48px;
    position: absolute;
    right: 43px;
    top: 142px;
    background: url(~img/how-it-works__heart.svg) 0 0 no-repeat;
    background-size: 100%;
  }

}

.howitworks__decor-3 {
  @include for-desktop {
    width: 365px;
    height: 127px;
    position: absolute;
    left: 69px;
    top: 368px;
    background: url(~img/how-it-works__must.svg) 0 0 no-repeat;
    background-size: 100%;
  }
}

.howitworks__decor-4 {
  @include for-desktop {
    width: 462px;
    height: 456px;
    position: absolute;
    left: 20px;
    top: 56px;
    background: url(~img/how-it-works__buble.svg) 0 0 no-repeat;
    background-size: 100%;
    z-index: -1;
  }
}

.howitworks__decor-5 {
  position: absolute;
  background: url(~img/how-it-works__ears.png) 0 0 no-repeat;
  background-size: 100%;
  z-index: -1;
  width: 322px;
  height: 154px;
  left: -85px;
  top: -86px;
  transform: rotate(345deg) scale(-1, 1);

  @include for-small-tablet {
    width: 322px;
    height: 154px;
    left: 0;
    top: -86px;
    transform: none;
  }

  @include for-tablet {
    left: 10px;
  }

  @include for-small-desktop {
    width: 510px;
    height: 243px;
    left: -75px;
    top: -114px;
  }

  @include for-desktop {
    left: -5px;
  }

}







.howitworks__content {
  padding-top: 54px;

  @include for-desktop {
    padding-top: 200px;
  }
}

.howitworks__heading {
  font-family: $font__bold;
  font-size: 18px;
  line-height: 30px;
  color: $not__black;
  margin-bottom: 16px;

  @include for-desktop {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 20px;
  }
}

.howitworks__text {
  font-size: 18px;
  line-height: 30px;
  color: $boring__gray;
}



