.get-quote-for-block {
  padding: 25px 0 126px;
  position: relative;
  z-index: 2;
  background-color: $blue__baby-light;

  @include for-tablet {
    padding: 55px 0 70px;
  }

  .container {
    position: relative;
    z-index: 1;
  }
}

.get-quote-for-cats::after {
  content: '';
  width: 315px;
  height: 92px;
  background: url(~img/get-qoute-for-cats.png) 0 0 no-repeat;
  background-size: 100%;
  position: absolute;
  left: 50%;
  bottom: -80px;
  z-index: 1;
  transform: translateX(-50%);

  @include for-tablet {
    width: 418px;
    height: 140px;
    left: 0;
    transform: translateX(-38%);
  }
}

.get-quote-for-dogs::after {
  content: '';
  width: 284px;
  height: 194px;
  background: url(~img/get-qoute-for-dogs.png) 0 0 no-repeat;
  background-size: 100%;
  position: absolute;
  right: 50%;
  bottom: -140px;
  z-index: 1;
  transform: translateX(50%);


  @include for-tablet {
    width: 304px;
    height: 208px;
    transform: none;
    right: 0;
    bottom: -50px;
  }

  @include for-desktop {
    width: 400px;
    height: 274px;
  }
}

.get-quote-for-block-container {
  border-radius: 20px;
  background-color: #fff;
  padding: 40px;
  box-shadow: $card__shadow;

  @include for-tablet {
    padding: 80px 56px;
  }
}

.get-quote-for-block__headline {
  font-family: $font__bold;
  font-size: 24px;
  line-height: 36px;
  color: $not__black;
  margin: 0 0 24px;
  display: block;
  text-align: center;

  @include for-tablet {
    font-size: 44px;
    line-height: 64px;
    margin: 0 0 40px;
  }
}

.get-quote-for-block__link {
  @include base-button(arrow, yellow);
}

