.hero-about-us {
  padding-top: 120px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  background: $green__normal url(~img/paws_bg-green.png) top center no-repeat;

  @include for-tablet {
    padding-top: 170px;
  }

  &:after {
    width: 120%;
    height: 200px;
    content: '';
    background: #fff;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 50% 50% 0% 0% / 50px 50px 0% 0%;
    z-index: -1;
  }
}

.hero-about-us__heading {
  font-size: 28px;
  line-height: 34px;
  color: $not__black;
  font-family: $font__bold;
  padding: 0 0 50px;
  text-align: center;
  font-weight: 400;
  margin: 0;

  @include for-tablet {
    font-size: 44px;
    line-height: 54px;
    padding: 0 0 110px;
  }

  strong {
    font-family: $font__bold;
  }
}

.misson-card {
  text-align: center;
  padding: 50px 34px;
  border-radius: 20px;
  background-color: #fff;
  box-shadow: $card__shadow;
  margin-bottom: 50px;

  @include for-tablet {
    padding: 60px;
    margin-bottom: 80px;
  }
}

.mission-card__heading {
  font-size: 24px;
  line-height: 36px;
  color: $not__black;
  font-family: $font__bold;
  font-weight: bold;
  margin: 0;
}

.mission-card__text {
  font-size: 24px;
  line-height: 36px;
  color: $not__black;
}

