.faq {
  background-color: #fff;
  padding: 0 0 25px;

  @include for-tablet {
    padding: 0 0 55px;
    // overflow: hidden;
  }

  .container {
    position: relative;
    z-index: 1;
  }
}

.faq__heading {
  text-align: center;
  display: block;
  font-family: $font__bold;
  color: $blue__normal;
  font-size: 24px;
  line-height: 36px;
  padding: 14px 0;
  margin: 0;

  @include for-tablet {
    font-size: 36px;
    line-height: 50px;
    padding: 60px 0;
  }
}

.faq__item {
  border-radius: 10px;
  background-color: #fff;
  border: 1px solid rgba(43, 41, 45, 0.2);
  box-shadow: $card__shadow;
  color: $not__black;
  margin-bottom: 20px;
  transition: all 0.2s;
  position: relative;

  @include for-tablet {
    border-radius: 20px;
  }

  &:hover,
  &.is-active {
    color: #fff;
    background-color: $blue__normal;
    border-color: $blue__normal;
    box-shadow: 0px 16px 48px rgba(65, 177, 241, 0.5);
  }

  &:after {
    display: block;
    content: '';
    width: 12px;
    height: 7px;
    background: url(~img/faq-arrow.svg) top left no-repeat;
    position: absolute;
    right: 40px;
    top: 35px;
    transition: transform 0.2s;
  }

  &.is-active::after {
    transform: rotate(180deg);
    transform-origin: center center;
  }
}




.faq__question {
  font-size: 18px;
  line-height: 30px;
  color: currentColor;
  font-family: $font__bold;
  padding: 25px 60px 24px 40px;
  cursor: pointer;
  transition: max-height .3s ease-in-out;
}

.faq__answer {
  padding: 0 40px 40px;
  color: currentColor;
  font-size: 18px;
  line-height: 30px;
  display: none;
  transition: all 0.5s;
  // max-height: 0;

  .is-active & {
    display: block;
  }
}

.faq-cat {
  display: none;

  @include for-small-desktop {
    width: 260px;
    height: 210px;
    background: url(~img/faq-cat.png) 0 0 no-repeat;
    background-size: 100%;
    position: absolute;
    bottom: -100px;
    left: -20px;
    z-index: -1;
    display: block;
  }
}

